<template>
	<toolbar text="">
		<span class="icon-chevron-left" slot="leftBtn" @click="back"></span>
		<span class="icon-refresh" slot="rightBtn" @click="loadUserInfo"></span>
	</toolbar>
	<section >
		<input type="button" @click="showDialog">

		<datepicker></datepicker>
	</section>

	<loading :loading="isload"></loading>
	<dialog :show.sync="isShowDialog" :title="dialogTitle" v-on:child-confirm="confirm">
		<div slot="dlg-body">hahah</div>
	</dialog>
</template>


<script lang="babel">
	
	import ToolBar from '../components/ToolBar.vue'
	import Loading from '../components/Loading.vue'
	import Dialog from '../components/Dialog.vue'
	import DatePicker from '../components/DatePicker.vue'
	export default {
		created(){
			//
			//1:
			setTimeout(()=>{
				this.isload=false;
			}, 1000 );
		},
		props:{

		},
		methods:{
			back(){
				history.back();
			},
			//
			loadUserInfo(){
				this.isload=true;
				setTimeout(()=>{
					this.isload=false;
				}, 1000 );
			},
			showDialog(){
					this.isShowDialog=!this.isShowDialog;
			},
			confirm(){
				alert("")
			}
		},
		data(){
			return {
				isload:true,
				isShowDialog:false,
				dialogTitle:''
			}
		},
		components:{
			toolbar:ToolBar,
			loading:Loading,
			dialog:Dialog,
			datepicker:DatePicker
		}

	}


</script>

<style type="text/css">
	
</style>


