<template>
     <div class="calendar">
        <div class="calenarDayView" v-show="showDay">
            <div class="calendar-header">
                <div class="calendar-title">
                    <div>
                        <span class="icon-double-angle-left" @click="yearClick(0)"></span>
                        <span class="icon-angle-left" style='margin-left:20px;width:50px;' @click="monthClick(0)"></span>
                    </div>
                    <p>
                        <span @click="showYearView">{{curYear}}</span>
                        <span @click="showMonthView">{{curMonth}}</span>
                    </p>
                    <div>
                         <span class="icon-angle-right" style='margin-right:20px;' @click="monthClick(1)"></span>
                         <span class="icon-double-angle-right" @click="yearClick(1)"></span>
                    </div>
                </div>
                <div class="calendarDay-week">
                    <ul>
                        <li v-for="(index,item) in weekRange" :class="{'restDay':index==5||index==6}">{{item}}</li>
                    </ul>
                </div>
            </div>
            <div class="calendar-range">
                <ul>
                    <li v-for="item in dateRange" class="calendarDay"   v-on:click="selectDay(item.day)">
                        <span v-bind:class="{'itemSelect':isCurSelect('D',item.day),'restDay':item.isRestDay,'curMonth':item.isCur}">{{item.day}}</span>
                    </li>
                </ul>
            </div>
            <div class="calendarDay-footer">
                <span @click="today"></span>
            </div>
        </div>
        <div class="calendarMonthView" v-show="showMonth">
            <div class="calendar-header">
                <div class="calendar-title">
                    <div>
                        <span class="icon-double-angle-left" @click="yearClick(0)"></span>
                    </div>
                    <p>
                        <span @click="showYearView">{{curYear}}</span>
                    </p>
                    <div>
                         <span class="icon-double-angle-right" @click="yearClick(1)"></span>
                    </div>
                </div>
            </div>
            <div class="calendar-range">
                <ul>
                    <li v-for="item in monthRange" class="calendarMonth"   v-on:click="selectMonth(item.id)">
                        <span v-bind:class="{'itemSelect':isCurSelect('M',item.id)}">{{item.text}}</span>
                    </li>
                </ul>
            </div>
        </div>
        <div class="calendarYearView" v-show="showYear">
            <div class="calendar-header">
                <div class="calendar-title">
                    <div>
                        <span class="icon-double-angle-left" @click="yearClick(0)"></span>
                    </div>
                    <p>
                        <span @click="showYearView">{{yearTitle}}</span>
                    </p>
                    <div>
                         <span class="icon-double-angle-right" @click="yearClick(1)"></span>
                    </div>
                </div>
            </div>
            <div class="calendar-range">
                <ul>
                    <li v-for="item in yearRange" class="calendarMonth"   v-on:click="selectYear(item)">
                        <span v-bind:class="{'itemSelect':isCurSelect('Y',item)}">{{item}}</span>
                    </li>
                </ul>
            </div>
        </div>
    </div>
</template>

<script lang="babel">
    
    export default {
        data:function(){
            return {
               curYear:0,
               curMonth:0,
               curDay:0,
               showDay:true,
               showYear:false,
               showMonth:false,
               weekRange:['','','','','','',''],
               monthRange:[
                           {id:1,text:''},{id:2,text:''},{id:3,text:''},{id:4,text:''},
                           {id:5,text:''},{id:6,text:''},{id:7,text:''},{id:8,text:''},
                           {id:9,text:''},{id:10,text:''},{id:11,text:''},{id:12,text:''}
                          ],
               dateRange:[],
               yearRange:[],
               yearTitle:''
            }
        },
        props:{
            width:{

            },
            //
            format:{
                type:String,
                default:"DD"//YYYY,MM,DD,H,M,S
            }
        },
        created:function(){
            //,
            var date=new Date();
            this.curYear=date.getFullYear();
            this.curMonth=date.getMonth()+1;
            this.curDay=date.getDate();

            this.dateRange=this.getDateRange(this.curYear,this.curMonth);
            this.yearRange=this.getYearRange(this.curYear);
            this.yearTitle=this.yearRange[0]+"~"+this.yearRange[11];
            //
            this.initShowView();
        },
        methods:{
            initShowView(){
                switch(this.format){
                    case "YYYY":
                        this.showYear=true;
                        this.showMonth=false;
                        this.showDay=false;
                        break;
                    case "MM":
                        this.showYear=false;
                        this.showMonth=true;
                        this.showDay=false;
                        break;
                    case "DD":
                        this.showYear=false;
                        this.showMonth=false;
                        this.showDay=true;
                        break;
                }
            },
            isCurSelect(flag,item){
                var date=new Date();
                const tempY=date.getFullYear();
                const tempM=date.getMonth()+1;
                if(flag=="D"){
                    return this.curYear==tempY&&this.curMonth==tempM&&this.curDay==item;
                }else if(flag=="M"){
                    return this.curYear==tempY&&tempM==item;
                }else{
                    return tempY==item;
                }
            },
            showYearView(){
                this.showYear=true
                this.showMonth=false;
                this.showDay=false;
            },
            showMonthView(){
                this.showYear=false
                this.showMonth=true;
                this.showDay=false;
            },
            selectYear:function(year){
                this.curYear=year;
                this.showYear=false
                this.showMonth=true;
                this.showDay=false;
                if(this.format=="YYYY"){
                    this.$dispatch('itemClick',year);
                }
            },
            selectMonth(month){
                this.showYear=false
                this.showMonth=false;
                this.showDay=true;
                this.curMonth=month;
                this.dateRange=this.getDateRange(this.curYear,this.curMonth);
                if(this.format=="MM"){
                    this.$dispatch('itemClick',this.curYear+"-"+month);
                }
            },
            //
            selectDay:function(day){
                const tempD=this.curYear+"-"+this.curMonth+"-"+day;
                console.log(""+tempD);
                this.$dispatch('item-click',tempD);
            },
            today:function(){
                var newDate=new Date();
                this.curYear=newDate.getFullYear();
                this.curMonth=newDate.getMonth()+1;
                this.dateRange=this.getDateRange(this.curYear,this.curMonth);
            },
            //
            yearClick(flag){
                if(this.showYear){
                    //
                    const year=flag==0?this.curYear-10:this.curYear+10;
                    this.yearRange=this.getYearRange(year);
                    this.yearTitle=this.yearRange[0]+"~"+this.yearRange[11];    
                }else{
                     const year=flag==0?this.curYear-1:this.curYear+1;
                     this.curYear=year;
                     this.dateRange=this.getDateRange(year,this.curMonth);
                }
              
            },
            monthClick(flag){
                var tempM=this.curMonth;
                var tempY=this.curYear;
                //
                if(flag==0){//
                    if(tempM==1){
                        tempM=12;
                        tempY=this.curYear-1;
                    }else{
                        tempM=tempM-1;
                    }
                }else{//
                    if(tempM==12){
                        tempY=this.curYear+1;
                        tempM=1;
                    }else{
                        tempM=tempM+1;
                    }
                }
                this.curYear=tempY;
                this.curMonth=tempM;
                console.log(tempY,tempM);
                 this.dateRange=this.getDateRange(tempY,tempM);
            },

            //
            dayOfWeek:function(date){
              var week=new Date(date).getDay();
              if(week==0){
                  return 7;
              }  
              else{
                  return week;
              }
            },
            //
            dayNumOfMonth:function(year,month){
                var d = new Date(year,month,0);
                return d.getDate();
            },
            //
            getDateRange:function(year,month){
                var datearray=[];
                var firstDateDay=0;
                //1:
                var firstDay=year+"-"+month+"-01";
                
                var firstWeek=this.dayOfWeek(firstDay);
                //2:
                if(firstWeek==1){
                    firstDateDay=1;
                }else{
                    //
                    var preMonth=0;
                    if(month==1){
                        preMonth=this.dayNumOfMonth(year-1,12);
                    }
                     preMonth=this.dayNumOfMonth(year,month-1);
                     
                     for(var pi=preMonth-firstWeek+2;pi<=preMonth;pi++){
                            var week=new Date(this.curYear+"/"+(this.curMonth-1)+"/"+pi).getDay();
                            var restDay=(week==6||week==0);
                            datearray.push({
                                isCur:false,
                                day:pi,
                                isRestDay:restDay
                            });
                     }
                }
                //3:
                var curDays=this.dayNumOfMonth(year,month);
                for(var i=1;i<=curDays;i++){
                    var week=new Date(this.curYear+"/"+this.curMonth+"/"+i).getDay();
                    var restDay=(week==6||week==0);
                    datearray.push({
                        isCur:true,
                        day:i,
                        isRestDay:restDay
                    });
                }
                //2:42,,
                if(datearray.length<42){
                    var nextValue=42-datearray.length;
                     for(var ni=1;ni<=nextValue;ni++){
                        var week=new Date(this.curYear+"/"+(this.curMonth+1)+"/"+ni).getDay();
                        var restDay=(week==6||week==0);
                        datearray.push({
                                isCur:false,
                                day:ni,
                                isRestDay:restDay
                        });
                     }
                }
                return datearray;
            },
            getYearRange:function(year){
                var tempYRange=[];
                for (var i = year - 6; i <=year+5; i++) {
                   tempYRange.push(i);   
                };
                return tempYRange;
            }
        }
    }

</script>

<style>
        .calendar {
            width: 100%;
            height: auto;
            display: flex;
            display: -webkit-flex;
            flex-flow: column nowrap;
            justify-content: flex-start;
            box-shadow: 4px 4px 5px gray;
            border: 2px solid whitesmoke;
            color: #666;
            font-size: 14px;

        }
        
        .calendar span {
            cursor: pointer;
        }
        
        .calendar-header {
            height: auto;
            display: flex;
            flex-flow: column nowrap;
            justify-content: center;
            border-bottom: 1px solid lightgrey;

        }
        
        .calendar-title {
            height: 30px;
            display: flex;
            padding: 5px 15px;
            flex-flow: row nowrap;
            justify-content: space-between;
            align-items: center;
            border-bottom: 1px solid lightgrey;
            font-weight: bold;
        }
        
        .calendarDay-title>span:active {
            color: #2db7f5;
        }
        
        .calendarDay-week{
            background-color: whitesmoke;
        }
        
        .calendarDay-week>ul {
            display: flex;
            flex-flow: row nowrap;
            justify-content: space-around;
            height: 30px;
             padding: 5px 0px;
            list-style-type: none;
            align-items: center;
            margin: 0px;
        }
        

        .calendarDay-week>ul>li {
            flex-basis: 14%;
            text-align: center;
        }
       

        .calendar-range {
            height: 260px;
        }


        
        .calendar-range>ul {
            display: flex;
            flex-flow: row wrap;
            justify-content: flex-start;
            padding: 5px 0px;
            list-style-type: none;
            align-items: center;
            margin: 0px;
            font-size: 16px;
        }

         .calendar-range>ul>li>span:hover{
            border:2px solid darkorange;
            color: darkorange;
            border-radius: 4px;
            padding: 5px;
         }

         .calendar-range>ul>li>span:active{
            border:2px solid darkorange;
            color: #2db7f5;
            border-radius: 4px;
            padding: 5px;
         }

        
        .calendarDay {
            flex-basis: 14.28%;
            height: 40px;
            line-height: 40px;
            text-align: center;
            cursor: pointer;
        }

        .calendarMonth{
            flex-basis: 30%;
            height: 60px;
            line-height: 40px;
            text-align: center;
            cursor: pointer;
        }
       
        .itemSelect {
            padding: 5px;
            border:2px solid #2db7f5;
            color: #2db7f5;
            border-radius: 4px;
        }
        .restDay{
            color: #e02d2d;
        }
       

        .curMonth{
            font-weight: bold;
        }
        
        .calendarDay-footer {
            height: 30px;
            padding: 5px 15px;
            flex-flow: row nowrap;
            border-top: 1px solid lightgray;
            display: flex;
            flex-flow: row nowrap;
            justify-content: center;
            align-items: center;
            font-weight: bold;
             background-color: whitesmoke;
        }
</style>
   



